<template>
  <transition enter-active-class="animated fadeInUpBig" leave-active-class="animated fadeOutDownBig">
  <div class="new_address" v-show="isShow" ref='editAddress'>
    <div class="new_header">
      <x-header :left-options="{showBack:false,backText:''}">
        <span class="back_btn" @click="hide"><i></i></span>
         <span class="save" @click="saveAddress" :class="{'color:#000':isDisable}"></span></x-header>
    </div>
    <div class="new_content">

      <group>
        <x-input title=""  placeholder=""  v-model="_address.contacts"></x-input>
        <x-input title=""  placeholder="" v-model="_address.phone" keyboard="number" is-type="china-mobile" ref="phoneTxt"></x-input>
        <x-address  title=""  placeholder="" v-model="addressVal" :list="addressData" confirm-text="" cancel-text=""></x-address>
        <x-textarea  placeholder="" v-model="_address.address" :max="60" ref="addressTxt"></x-textarea>
      </group>
      <group>
        <x-switch title="" v-model="isFirst"></x-switch>
      </group>
    </div>
  </div>
  </transition>
</template>

<script>
  import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'
import value2name from 'vux/src/filters/value2name.js'

  import {post} from '../../common/js/public-resource'
  import {getStorage,setStorage} from '../../common/js/storage'
  import {USERINFO_STORAGE} from '../../common/public-variable'
  import {isLogin} from '../../common/js/checkIsLogin'
  export default {
    props: {
      _address: {
        contacts: '',//	//
        phone: '',//
        province: '',//
        city: '',//
        area: '',//
        address: '',//
        first: '',// 12
        state: ''// 12
      }
    },
    components: {
      ChinaAddressData,
      XAddress,
      XSwitch,
      XTextarea,
      XHeader,
      Cell,
      Group,
      XInput
    },
    data () {
      return {
        addressData: ChinaAddressData,
        isShow: false,
        addressVal: [],
        isFirst: false,
        isDisable: false
      }
    },
    methods: {
      show(){
        this.isShow = true;
      },
      hide(){
        this.isShow = false;
      },
      saveAddress(){
        var that = this;
        if (that._address) {
          if (!that._address.contacts) {
            that.$vux.toast.show({text: '!', type: 'text', width: '18em'});
          }
          else if (!that._address.phone) {
            that.$vux.toast.show({text: '!', type: 'text', width: '18em'});
          }
          else if (!that._address.address) {
            that.$vux.toast.show({text: '!', type: 'text', width: '18em'});
          }
          else if (!that.$refs.phoneTxt.valid) {
            that.$vux.toast.show({text: '!', type: 'text', width: '18em'});
          }
          else if (that.addressVal.length <= 2) {
            that.$vux.toast.show({text: '!', type: 'text', width: '18em'});
          }
          else {
            if (isLogin()) {
              var userInfo = getStorage(USERINFO_STORAGE);
              that._address.province = that.addressVal[0];
              that._address.city = that.addressVal[1];
              that._address.area = that.addressVal[2];
              that._address.first = that.isFirst ? 1 : 2;
              post('usercenter/address/addAddress', {
                token: userInfo.token,
                contacts: that._address.contacts,//	//
                province: that._address.province,//
                city: that._address.city,//
                area: that._address.area,//
                address: that._address.address,//
                phone: that._address.phone,//
                first: that._address.first// 12
              }, function (data, success) {
                if (success) {
                  that.$vux.toast.show({text: '!', width: '18em'});
                  that.$emit('child-update', that.address);//
                }
                else {
                  that.$vux.toast.show({text: data,type:'text', width: '18em'});
                }
              })
            } else {
              that.$vux.toast.show({text: '!', width: '18em'});
              that.$router.go({path: '/login'});
            }
          }
        }
        else {
          that.$vux.toast.show({text: '!',type:'warn', width: '18em'});
        }
      }
    }
  }
</script>

<style rel="stylesheet/less" lang="less">
  @import "../../assets/css/newaddress.less";
</style>



